              p.mb-0 Esta Norma relaciona los procedimientos aplicables a los activos, con el fin de reflejar el valor real de los hechos econmicos. El deterioro hace referencia a la prdida que sufren los activos cuando el valor registrado en libros es mayor al importe recuperable; es decir, el valor al cual se pretende vender o valorar dicho activo, menos los costos y uso del bien.
        p.mb-0 De acuerdo con la NIC 36, el importe recuperable es el mayor entre su valor razonable menos los costos de disposicin y su valor en uso (p.1695).
          br
          br
          |Segn la NIIF 13 (2020) la medicin del valor razonable es el precio que se recibira por vender un activo o que se pagara por transferir un pasivo en una transaccin ordenada entre participantes de mercado en la fecha de la medicin (p.3).
          br
          br
          |#[spam.fw-bold El objetivo y alcance de la presente norma son los siguientes:]
      .col-lg-5.col-7: img(src='@/assets/curso/temas/9.png', alt='')
    .titulo-tres
      h3 El objetivo
    .mb-4(style="background-color: #FED6A8")
      .row.justify-content-center.align-items-center.mb-5
        .col-lg-5.col-7.mb-lg-0.mb-3: img(src='@/assets/curso/temas/3.png', alt='')
        .col-lg-7
          .p-4: p.mb-0 El objetivo de esta Norma consiste en establecer los procedimientos que una entidad aplicar para asegurar que sus activos estn contabilizados por un importe que no sea superior a su importe recuperable. Un activo estar contabilizado por encima de su importe recuperable cuando su importe en libros exceda del importe que se pueda recuperar del mismo a travs de su utilizacin o de su venta. Si este fuera el caso, el activo se presentara como deteriorado, y la norma exige que la entidad reconozca una prdida por deterioro del valor de ese activo. En la Norma tambin se especifica cundo la entidad revertir la prdida por deterioro del valor, as como la informacin a revelar.
    .titulo-tres
      h3 Alcance
    .row.justify-content-center.align-items-center.mb-5
      .col-lg-7.mb-lg-0.mb-3
