          li.d-flex
            i.fas.fa-angle-right
            p.mb-0 Activos por impuestos diferidos (vase la NIC 12 Impuesto a las Ganancias).

          li.d-flex
            i.fas.fa-angle-right
            p.mb-0 Activos que surjan por beneficios a los empleados (vase la NIC 19 Beneficios a los Empleados).

          li.d-flex
            i.fas.fa-angle-right
            p.mb-0 Activos financieros dentro del alcance de la NIIF 9 Instrumentos Financieros.

          li.d-flex
            i.fas.fa-angle-right
            p.mb-0 Propiedades de inversin que se midan segn su valor razonable (vase la NIC 40 Propiedades de Inversin).

          li.d-flex
            i.fas.fa-angle-right
            p.mb-0 Activos biolgicos relacionados con la actividad agrcola dentro del alcance de la NIC 41 Agricultura que se midan segn su valor razonable menos los costos de disposicin.

          li.d-flex
            i.fas.fa-angle-right
            p.mb-0 Contratos dentro del alcance de la NIIF 17 Contratos de Seguros que son activos.

          li.d-flex
            i.fas.fa-angle-right
            p.mb-0 Activos no corrientes (o grupos de activos para su disposicin) clasificados como mantenidos para la venta de acuerdo con la NIIF 5 Activos no Corrientes Mantenidos Para la Venta y Operaciones Discontinuadas
