            p.mb-0 El importe del deterioro debe registrarse en el resultado del perodo, excepto los activos reconocidos por su valor revaluado de acuerdo con la Norma Internacional de Contabilidad (NIC) 16.

          li.d-flex
            i.fas.fa-angle-right
            p.mb-0 Para identificar el valor razonable del activo, se tiene en cuenta analizar el precio de cotizacin en el mercado activo.

          li.d-flex
            i.fas.fa-angle-right
