          li.d-flex
            i.fas.fa-angle-right
            p.mb-0 Cuando existe indicios de deterioro, es indispensable estimar el importe recuperable de forma individual o en su defecto de la unidad generadora de efectivo (conjunto de activos que generan entradas de efectivo) siempre y cuando no est por debajo del mayor valor entre los siguientes: el valor razonable menos los costos de disposicin, el valor en uso y cero.

          li.d-flex
            i.fas.fa-angle-right
            p.mb-0 Cuando se reconoce el deterioro de la unidad generadora del activo, se realiza la distribucin (reduccin de importe en libros) entre el conjunto de activos que lo componen.

          li.d-flex
            i.fas.fa-angle-right
