                p.mb-0 Durante el perodo, las tasas de inters de mercado, u otras tasas de mercado de rendimiento de inversiones, han experimentado disminuciones que probablemente afecten a la tasa de descuento utilizada para calcular el valor en uso del activo, de forma que incrementan el importe recuperable de forma significativa.
          .p-4(titulo="Fuentes internas de informacin")
            ul.lista-ul
