    .titulo-tres
      h3 Deterioro en inventarios (NIC 2)
    .row.justify-content-center.mb-5
      .col-lg-7.mb-lg-0.mb-3
        .tarjeta.p-4(style="background-color: #DDEFFD").rounded-0.mb-4
          .row.justify-content-center.align-items-center
            .col-2.mb-lg-0.mb-3: img(src='@/assets/curso/temas/17.svg', alt='')
            .col-lg-10
              p.mb-0 Esta norma se aplica a todos los inventarios; excepto instrumentos financieros y activos biolgicos. All se definen los criterios de reconocimiento, medicin, presentacin y revelacin de inventarios, permitiendo identificar el costo con base a los elementos que lo componen. De igual manera, presenta las bases para el reconocimiento y medicin del deterioro por este concepto.
        p.mb-0 El deterioro en inventario se identifica mediante la comparacin del importe en libros y el valor neto realizable; este ltimo hace referencia al precio que se puede obtener por la venta de dicho activo. Si el importe en libros es mayor al valor neto realizable se considera deterioro de inventarios; por lo tanto, se registra la prdida mediante un gasto.
