    #t_1_2.titulo-segundo.color-acento-contenido(data-aos='fade-right')
      h2 1.2 Depreciaciones
    .row.justify-content-center.mb-5
      .col-lg-5.col-7.mb-lg-0.mb-3: img(src='@/assets/curso/temas/20.svg', alt='')
      .col-lg-7
        p.mb-4 De acuerdo con los estndares internacionales la depreciacin se debe realizar a partir de que el activo fijo se encuentra apto para su uso, teniendo en cuenta el valor del importe en libros y la vida til estipulada en las polticas contables. La estimacin de la vida til es el perodo en el cual la empresa desea darle uso al activo fijo.
          br
          br
          |Por ejemplo, la empresa Contable SAS, adquiere un vehculo para uso de la gerencia, en las polticas contables se estableci que la vida til es de tres (3) aos, teniendo en cuenta que cada tres aos venden el vehculo y adquieren uno nuevo, por lo tanto, este ser el perodo de depreciacin.
        .p-4(style="background-color: #DDEFFD ")
          p.mb-0 De igual manera, se tiene en cuenta el desgaste del activo fijo y la obsolescencia tcnica o comercial, teniendo en cuenta las mejoras, mantenimientos y reparaciones realizadas. En el caso en que la empresa, cambie las estimaciones contables respecto a la vida til o valor residual, deber revelar la informacin teniendo en cuenta los efectos que generen las modificaciones sobre los resultados futuros. Estos cambios se reconocern como un cambio en una estimacin contable de acuerdo con la NIC 8 Polticas Contables, Cambios en las Estimaciones Contables y Errores.
    .row.justify-content-center.mb-5
      .col-lg-7.mb-lg-0.mb-3
        p.mb-4 Es importante definir el valor residual, teniendo en cuenta que es un trmino utilizado frecuentemente en el procedimiento de clculo de la depreciacin de activos fijos.
        .bloque-texto-d.color-secundario.p-4.mb-4
          .bloque-texto-d__texto.mb-2
            i.fas.fa-quote-left
            h3.text-regular El valor residual de un activo es el importe estimado que la entidad podra obtener de un activo por su disposicin, despus de haber deducido los costos estimados para su disposicin, si el activo tuviera ya la edad y condicin esperadas al trmino de su vida til.
            i.fas.fa-quote-right
          .bloque-texto-d__autor 
            .h5.mb-0 Segn la NIC 16 (2020)
        p.mb-4 De igual forma, para ampliar los conceptos sobre este tema se analizan los prrafos 43 y 44 de la NIC 16, que manifiesta que se depreciar de forma separada cada parte de un elemento de propiedades, planta y equipo que tenga un costo significativo con relacin al costo total del elemento y ser distribuida entre sus partes significativas. Por ejemplo, podra ser adecuado depreciar por separado la estructura y los motores de un avin.
          br
          br
          |La depreciacin de un activo cesar cuando haya finalizado su perodo de depreciacin (vida til). Por otro lado, se tendr en cuenta la fecha ms prxima entre la clasificacin del activo como mantenido para la venta o la baja de cuentas de este, pero en ningn momento cuando el activo no se est utilizando.
          br
          br
          |Para calcular la depreciacin contable, se tiene en cuenta los siguientes elementos para hallar el importe en libros:
      .col-lg-5.col-7.mb-lg-0.mb-3
        img(src='@/assets/curso/temas/21.svg', alt='')   
