    p.mb-4 En el caso que se realicen las mediciones posteriores y que el activo se revale, el nuevo importe se tendr en cuenta para la depreciacin de este. 
    .row.justify-content-center.mb-5
      .col-lg-7.mb-lg-0.mb-3
        .titulo-tres
          h3 Depreciacin fiscal
        .tarjeta.p-4(style="background-color: #DDEFFD").rounded-0.mb-4
          .row.justify-content-center.align-items-center
            .col-2.mb-lg-0.mb-3: img(src='@/assets/curso/temas/23.svg', alt='')
            .col-lg-10
              p.mb-0 De acuerdo con el art. 69 del Estatuto Tributario (ET) para los contribuyentes declarantes de renta, se tendr en cuenta la sumatoria de los costos de adquisicin y los costos atribuibles hasta que el activo se encuentre en condiciones de uso. Cabe resaltar que los costos de desmantelamiento y retiro del elemento no se tendrn en cuenta.
        p.mb-4 #[b Pargrafo 1.] Las propiedades de inversin que se midan contablemente bajo el modelo de valor razonable, para efectos fiscales se medirn al costo.
          br
          br
          |Segn el art. 69-1, los activos no corrientes mantenidos para la venta tendrn en cuenta los siguientes conceptos para calcular la depreciacin acumulada:      
