        .titulo-tres.mb-4
          h3 Mtodos de depreciacin
        p.mb-4 La depreciacin se calcula sobre la sumatoria del precio de adquisicin, el impuesto sobre las ventas y costos atribuibles para poner en funcionamiento el activo. Los mtodos, son los siguientes:
        .d-inline-block.rounded-3.p-2.mb-3(style="background-color: #FED6A8") 
          h4.mb-0 Mtodo de lnea recta
        p.mb-2 Es el mtodo que se usa con mayor frecuencia en las empresas, el cual consiste en dividir el costo del activo entre la vida til estimada; por lo tanto, el clculo en perodos iguales ser el mismo valor.
        .row.justify-content-center.mb-3
