        p.mb-4 Es considerado un mtodo de depreciacin acelerada, porque el activo se deprecia ms en los primeros aos y menos en los ltimos. Cabe resaltar que el mtodo de depreciacin se estipula en las polticas contables, sealando la tasa de depreciacin y vida til aplicable a los activos fijos.
          br
          br
          |Primero, se debe calcular el factor de depreciacin, para ello, utilizamos la siguiente frmula:
