          p.mb-0 Por otro lado, los activos intangibles se amortizan a medida que generan beneficios econmicos para la empresa, teniendo en cuenta que estos poseen vida til definida e indefinida, estos ltimos de acuerdo con la NIC 38 no se amortizan; adems se tendr en cuenta los criterios relacionados en la NIC 38 y polticas diseadas por la empresa. Cabe resaltar, que los activos intangibles inician el proceso de amortizacin, una vez este se encuentren en condiciones de uso.

        .bloque-texto-d.color-secundario.p-4
          .bloque-texto-d__texto.mb-2
            i.fas.fa-quote-left
            p.text-regular La amortizacin cesar en la fecha ms temprana entre aquella en que el activo se clasifique como mantenido para la venta (o incluido en un grupo de activos para su disposicin que se haya clasificado como mantenido para la venta) de acuerdo con la NIIF 5, y la fecha en que se produzca la baja en cuentas de este. (prr.97)
              br
              br
              |Pueden utilizarse diferentes mtodos para distribuir el importe amortizable de un activo, de forma sistemtica, a lo largo de su vida til. Estos mtodos incluyen el mtodo lineal, el de depreciacin decreciente y el de las unidades de produccin. El mtodo utilizado se seleccionar a partir del patrn esperado de consumo de los beneficios econmicos futuros esperados incorporados en el activo y se aplicar de forma coherente de perodo a perodo, a menos que se produzca un cambio en los patrones esperados de consumo de dichos beneficios econmicos futuros.
