            p.text-regular Tanto el perodo como el mtodo de amortizacin utilizado para un activo intangible con vida til finita se revisarn, como mnimo, al final de cada perodo. Si la nueva vida til esperada difiere de las estimaciones anteriores, se cambiar el perodo de amortizacin para reflejar esta variacin. Si se ha experimentado un cambio en el patrn esperado de generacin de beneficios econmicos futuros por parte del activo, el mtodo de amortizacin se modificar para reflejar estos cambios. Los efectos de estas modificaciones en el perodo y en el mtodo de amortizacin, se tratarn como cambios en las estimaciones contables. (prr.106)
