      h2 1.4 Diferencia en cambio
    .row.justify-content-center.mb-5
      .col-lg-7.mb-lg-0.mb-3
        p.mb-4 Este tipo de ajustes se presenta cuando la empresa realiza operaciones comerciales en moneda extranjera o tiene negocios en el extranjero; por lo tanto, al realizar la conversin de moneda para la presentacin de la informacin contable la empresa se encuentra expuesta al riesgo cambiario, lo cual genera diferencias que deben registrarse mediante un ajuste contable, cuyo objetivo es reflejar la realidad econmica.
          br
          br
          |El objetivo de la Norma Internacional de contabilidad (NIC) 21 expresa:
        ol.lista-ol--cuadro
          li.d-flex
            .lista-ol--cuadro__vineta
              span 1
            p.mb-0 Una entidad puede llevar a cabo actividades en el extranjero de dos maneras diferentes. Puede realizar transacciones en moneda extranjera o bien puede tener negocios en el extranjero. Adems, la entidad puede presentar sus estados financieros en una moneda extranjera. El objetivo de esta Norma es prescribir cmo se incorporan, en los estados financieros de una entidad, las transacciones en moneda extranjera y los negocios en el extranjero, y cmo convertir los estados financieros a la moneda de presentacin elegida.
          li.d-flex
            .lista-ol--cuadro__vineta
              span 2
            p.mb-0 Los principales problemas que se presentan son la tasa o tasas de cambio a utilizar, as como la manera de informar sobre los efectos de las variaciones en las tasas de cambio dentro de los estados financieros. Cabe resaltar que para el grupo 2- NIIF pymes aplica la seccin 30 Conversin de la moneda extranjera. (p.A195)
      .col-lg-5.col-7: img(src='@/assets/curso/temas/32.svg', alt='')
    .row.justify-content-center.mb-5
      .col-1.mb-lg-0.mb-3: img(src='@/assets/curso/temas/14.png', alt='')
      .col-lg-11
        .d-inline-block.rounded-3.p-2.mb-3(style="background-color: #FED6A8")
          h4.mb-0 Ejemplo
        p.mb-4 El 24 de noviembre de 2020 la empresa Contables SAS adquiere maquinaria y equipo por valor de USD 57.000, a una tasa de USD 3.500. El 18 de diciembre de 2020, se transfiere el riesgo una vez la mquina se encuentra en Colombia, fecha en la cual se realizar el pago. Cuando se realiza el proceso de nacionalizacin la tasa vigente es USD 3.620.
