      h2 1.5 Arqueo de caja
    .row.justify-content-center.mb-5
      .col-lg-7.mb-lg-0.mb-3
        p.mb-0 Las empresas manejan la cuenta de caja general y caja menor, las cuales hacen parte del efectivo; esta ltima se utiliza para gastos de menor cuanta y debe establecerse en las polticas contables el valor del fondo y los conceptos que lo componen, con el fin de ser aplicados de forma correcta conforme los estndares establecidos.
          br
          br
          |Por otro lado, la caja general se utiliza para cancelar gastos o conceptos mayores. En cualquiera de los dos casos es importante realizar un arqueo de caja, el cual consiste en comparar el valor de libros con lo que efectivamente existe en la caja.
          br
          br
          |Cuando se realiza la comparacin pueden surgir diferencias que requieren un ajuste contable mediante una nota de contabilidad, estos pueden ser:
      .col-lg-5.col-7: img(src='@/assets/curso/temas/33.svg', alt='')
    ol.lista-ol--cuadro
      li.d-flex
        .lista-ol--cuadro__vineta
          span a
        p.mb-0 El importe de libros es mayor al valor del arqueo de caja: eso genera un faltante, por ende, se establece el responsable de dicha situacin y se procede a realizar el ajuste, con el fin de reflejar la realidad econmica y el saldo correcto del efectivo. Los casos que se presentan por faltantes son los siguientes:
    .fondo-sly.mn.p-5.mb-5
      .tarjeta.p-5.bg-white
        SlyderA(tipo='b')
          .row.justify-content-center
            p.mb-4.fw-bold Carece de registro en libros, sin embargo, el dinero se utiliz para cancelar algn concepto 
            .col-lg-5.mb-lg-0.mb-3
              h5 Ejemplo: 
              p.mb-0 Sandra Rojas cancel por concepto de papelera $40.000, sin embargo, no se registr dicho concepto en los libros. Por lo tanto, se realiza el ajuste, registrando el concepto pendiente.
                br
                br
                |Asiento de ajuste.
