      h2 1.6 Conciliacin bancaria
    .row.justify-content-center.mb-5
      .col-lg-7.mb-lg-0.mb-3
        .p-4.mb-4(style="background-color: #DDEFFD")
          p.mb-0 La conciliacin bancaria, es el proceso de verificacin y comparacin del importe en libros de la cuenta de bancos con el extracto de la cuenta de ahorros o corriente generado por la entidad financiera; cuyo objetivo, es confrontar las diferencias y realizar los ajustes respectivos que permitan revelar informacin comprensible, til y oportuna.
        p.mb-0 La conciliacin se realiza de forma mensual, teniendo en cuenta, confirmar las fechas, valores, soportes de las transacciones, luego se analizan las diferencias encontradas y se realizan los ajustes, con el fin de reflejar el saldo correcto en los libros. Los ajustes que frecuentemente se presentan son los siguientes:
      .col-lg-5.col-7: img(src='@/assets/curso/temas/34.png', alt='')
