        p.mb-4 El Balance de prueba o de comprobacin es un documento que refleja los saldos en el debe y haber de las cuentas de activo, pasivo, patrimonio, ingresos, gastos y costos. Cada una de las columnas dbito y crdito deber dar sumas iguales, verificando una vez ms el principio de partida doble.
        .tarjeta.p-4(style="background-color: #DDEFFD").rounded-0.mb-4
