              p.mb-0 Las cuentas de naturaleza dbito son activos, gastos y costo y las cuentas de naturaleza crdito se encuentran conformadas por pasivo, patrimonio e ingresos.
        p.mb-0 Este balance es el insumo para elaborar el balance de prueba ajustado, donde se actualizan los saldos de las cuentas que presentaron ajustes por diferentes conceptos y seguidamente realizar los estados financieros de propsito general.
