        p.mb-4 Para elaborar el balance ajustado es importante tener en cuenta los diferentes ajustes que se presentaron en las cuentas, como son los ajustes de caja, bancos, depreciaciones, amortizaciones y dems cuentas que hayan sufrido alguna modificacin por concepto de algn tipo de ajustes, los cuales generan cambios en perodos posteriores.
        .p-4.mb-4(style="background-color: #DDEFFD ")
          p.mb-0 Teniendo en cuenta lo anterior, se procede a realizar el comprobante de ajustes, seguidamente el balance ajustado donde se trasladan cada una de las cuentas de activos, pasivo, patrimonio, ingresos, gastos y costos.
        p.mb-0 Cabe resaltar que el balance debe sumar igual en el movimiento dbito y crdito.
